/* global document, Office */

Office.onReady((info) => {
  if (info.host === Office.HostType.Outlook) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";

    retrieveMailData();
  }
});


function retrieveMailData(){
  var sender = Office.context.mailbox.item.sender;
  var subject = Office.context.mailbox.item.subject;
  var attachments = Office.context.mailbox.item.attachments;

  loadMailData(sender.emailAddress, subject, attachments);
}

function loadMailData(senderEmailAddress, subject, attachments = []){
  if(senderEmailAddress == null || subject == null || attachments == null){
      // TODO: Couldnt retrieve data, display error
      return;
  }
  $("#sender-id").text(senderEmailAddress);
  $("#subject-id").text(subject); 
  if(attachments.length > 0){
      console.log("SOI-LOG: attachments found");
      $("#attachments-container").css("display", "block");
      for(let i = 0; i < attachments.length; i++){
          var attachment = attachments[i];
          $("#attachments-id").append("<li>" +attachment.name + ", "+ attachment.size.formatBytes() +"</li>")
      }
  }
}

Number.prototype.formatBytes = function() {
  var units = ['B', 'KB', 'MB', 'GB', 'TB'],
      bytes = this,
      i;

  for (i = 0; bytes >= 1024 && i < 4; i++) {
      bytes /= 1024;
  }

  return bytes.toFixed(2) + units[i];
}

